import { DataService } from './../../../../services/data.service';
import { JsonService } from './../../../../services/json.service';
import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';

@Component({
  selector: 'app-students',
  templateUrl: './students.component.html',
  styleUrls: ['./students.component.scss']
})
export class StudentsComponent implements OnInit {




  selectedStudent: any;
  cities = [
    { name: 'New York', code: 'NY' },
    { name: 'Rome', code: 'RM' },
    { name: 'London', code: 'LDN' },
    { name: 'Istanbul', code: 'IST' },
    { name: 'Paris', code: 'PRS' }
  ];

  studentList: any;
  selectedStudentFamilyinfo:  any;
  StudentHealthInformation: any;



  constructor(
    private jsonService: JsonService,
    private dataService: DataService
    ) { }

  ngOnInit(): void {
    this.getStudentList();
    this.getStudentFamilyInformation();
    this.getStudentHealthInformation();
    this.dataService.selectedStudent.subscribe(data=>{
      this.selectedStudent=data;
      console.log('StudentDetailsInfotrmation is',this.selectedStudent);      
    })
    this.dataService.selectedStudentFamilyinfo.subscribe(data=>{
      this.selectedStudentFamilyinfo=data;
      console.log('selectedStudentFamilyinfo is',this.selectedStudent);      
    })
    this.dataService.StudentHealthInformation.subscribe(data=>{
      this.StudentHealthInformation=data;
      console.log('StudentHealthInformation is',this.selectedStudent);      
    })

  }

  getStudentList() {
    this.jsonService.getStudentList()
      .subscribe(res => {
        let students: any = res;
        this.studentList = students['Records'];
        console.log('studentList', this.studentList.Records);

      }
      );
  }
  getStudentDetailsInfotrmation() {
    this.jsonService.getStudentDetailsInfotrmation()
      .subscribe(res => console.log(res)
      );
  }
  getStudentFamilyInformation() {
    this.jsonService.getStudentFamilyInformation()
      .subscribe(res =>{
        this.dataService.setStudentFamilyinfo(res);
        console.log('getStudentFamilyInformation',res)}
      );
  }

  getStudentHealthInformation() {
    this.jsonService.getStudentHealthInformation()
      .subscribe(res => {
        this.dataService.setStudentHealthInformation(res);
        console.log('getStudentHealthInformation',res)}
      );
  }
  getStudentInscriptionHistoryTable() {
    this.jsonService.getStudentInscriptionHistoryTable()
      .subscribe(res => console.log(res)
      );
  }
  onRowSelect(event: any) {
    console.log(event);

    // this.messageService.add({severity:'info', summary:'Product Selected', detail: event.data.name});
  }

  onRowUnselect(event: any) {
    console.log(event);

    // this.messageService.add({severity:'info', summary:'Product Unselected',  detail: event.data.name});
  }

  onStudentSelect(student:any){
    console.log(student);
    this.dataService.setStudent(student);    
    $('.student-list-table').removeClass('col-12').addClass('col-4 transition-05')
    
  }
}
