import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class JsonService {

  constructor(private httpService: HttpClient) { }

  getStudentList() {
    return this.httpService.get('../../assets/json/ListStudentOfClasseTable.json');
  }

  getStudentDetailsInfotrmation() {
    return this.httpService.get('../../assets/json/StudentDetailsInfotrmation.json');

  }
  getStudentFamilyInformation() {
    return this.httpService.get('../../assets/json/StudentFamilyInformation.json');

  }
  getStudentHealthInformation() {
    return this.httpService.get('../../assets/json/StudentHealthInformation.json');

  }
  getStudentInscriptionHistoryTable() {
    return this.httpService.get('../../assets/json/StudentInscriptionHistoryTable.json');

  }

}
