import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs/internal/BehaviorSubject';

@Injectable({
  providedIn: 'root'
})
export class DataService {
  selectedStudent: BehaviorSubject<any>;
  selectedStudentFamilyinfo: BehaviorSubject<any>;
  StudentHealthInformation: BehaviorSubject<any>;

  constructor() {
    this.selectedStudent = new BehaviorSubject('');
    this.selectedStudentFamilyinfo = new BehaviorSubject('');
    this.StudentHealthInformation = new BehaviorSubject('');

   }

   setStudent(studentObj:any) {
    this.selectedStudent.next(studentObj);
  }
   setStudentFamilyinfo(studentObj:any) {
    this.selectedStudentFamilyinfo.next(studentObj);
  }
   setStudentHealthInformation(studentObj:any) {
    this.StudentHealthInformation.next(studentObj);
  }
}
